.get-started1-get-started {
  display: flex;
  position: relative;
  background-color: #ff2f66;
}
.get-started1-text {
  color: #ffffff;
  font-size: 16px;
  font-style: normal;
  font-family: Poppins;
  font-weight: 500;
  line-height: 24px;
}
@media(max-width: 991px) {
  .get-started1-get-started {
    flex: 1;
  }
}
@media(max-width: 767px) {
  .get-started1-get-started {
    padding-top: var(--dl-space-space-unit);
    padding-bottom: var(--dl-space-space-unit);
  }
}
